import translations from "./translate.js";

document.addEventListener('DOMContentLoaded', () => {
  const modal = document.getElementById('languageModal');
  const savedLang = localStorage.getItem('lang');

  // –ü–æ–∫–∞–∑–∞—Ç—å –º–æ–¥–∞–ª–∫—É —Ç–æ–ª—å–∫–æ –µ—Å–ª–∏ —è–∑—ã–∫ –Ω–µ –≤—ã–±—Ä–∞–Ω
  if (!savedLang) {
    modal?.style.setProperty('display', 'flex');
  } else {
    setLanguage(savedLang);
    modal?.style.setProperty('display', 'none');
  }

  // –£–Ω–∏–≤–µ—Ä—Å–∞–ª—å–Ω—ã–π –æ–±—Ä–∞–±–æ—Ç—á–∏–∫ –¥–ª—è –≤—Å–µ—Ö –∫–Ω–æ–ø–æ–∫ —Å data-lang
  document.querySelectorAll('[data-lang]').forEach(button => {
    button.addEventListener('click', () => {
      const lang = button.dataset.lang;
      if (lang) {
        localStorage.setItem('lang', lang);
        setLanguage(lang);

        // –ó–∞–∫—Ä—ã—Ç—å –º–æ–¥–∞–ª–∫—É, –µ—Å–ª–∏ –Ω–∞–∂–∞–ª–∏ –≤–Ω—É—Ç—Ä–∏ –Ω–µ—ë
        modal?.style.setProperty('display', 'none');
      }
    });
  });
});

// üîÅ –£—Å—Ç–∞–Ω–∞–≤–ª–∏–≤–∞–µ—Ç –ø–µ—Ä–µ–≤–æ–¥—ã –ø–æ –∫–ª—é—á–∞–º –∏–∑ data-i18n
function setLanguage(lang) {
  if (typeof i18next !== 'undefined') {
    i18next.changeLanguage(lang);
  }

  const elements = document.querySelectorAll('[data-i18n]');

  elements.forEach(el => {
    const key = el.dataset.i18n;

    if (translations[lang] && translations[lang][key]) {
      // –î–æ–¥–∞—Ç–∏ –∫–ª–∞—Å –∑–Ω–∏–∫–Ω–µ–Ω–Ω—è
      el.classList.add('fading-out');

      // –ß–µ—Ä–µ–∑ 300–º—Å –∑–º—ñ–Ω–∏—Ç–∏ —Ç–µ–∫—Å—Ç —ñ –ø—Ä–∏–±—Ä–∞—Ç–∏ –∫–ª–∞—Å
      setTimeout(() => {
        el.innerHTML = translations[lang][key];
        el.classList.remove('fading-out');
      }, 300);
    }
  });
}